// Update HTML methods
const $currentScore = document.querySelector("#currentScore");
const $currentSpeed = document.querySelector("#currentSpeed");
const $startBtn = document.querySelector("#startBtn");

const generateRandomNumber = (min, max) =>
  Math.floor(Math.random() * (max - min)) + min;

const updateScoreDOM = (newScore) => ($currentScore.innerHTML = newScore);
const toggleStartBtn = (isPlaying) =>
  ($startBtn.innerHTML = isPlaying ? "Pause" : "Start");

const updateSliderHTML = (newValue) =>
  ($currentSpeed.innerHTML = `${newValue}`);

const updateScore = (state, ptsToAdd) => {
  state.score += +ptsToAdd;
  $currentScore.innerHTML = state.score;
};

const removeDot = (dot) => {
  dot.removeEventListener("animationend", removeDot);
  dot.parentNode.removeChild(dot);
};

// DOts methods

const getPosition = (dotSize) => {
  const maxWidth = document.querySelector(".game__board").clientWidth;
  const leftPosition = generateRandomNumber(0, maxWidth - dotSize);
  return leftPosition;
};

const getAnimationTime = (fallRate) => {
  const windowHeight = window.innerHeight;
  return windowHeight / fallRate;
};

const addPointToScore = (state, dot) => {
  if (state.isPlaying) {
    updateScore(state, dot.dataset.value);
    removeDot(dot);
  }
};

const resetGameBoard = (myInterval, initialSliderValue) => {
  updateScoreDOM(0);
  updateSliderHTML(initialSliderValue);
  toggleStartBtn(false);
  document.querySelector("#currentSpeed").value = initialSliderValue;
  document.querySelector("#speedControl").value = initialSliderValue;
  clearInterval(myInterval);

  // Remove all cages from dom
  const dots = document.querySelectorAll(".dot");
  dots.forEach((cage) => {
    removeDot(cage);
  });
};

const displayElement = (element) => {
  element.classList.addClass("show");
};

const pauseDots = (isPlaying = false) => {
  const $dots = document.querySelectorAll(".dot");
  $dots.forEach((dot) => {
    dot.style.WebkitAnimationPlayState = isPlaying ? "running" : "paused";
    if (isPlaying) {
      dot.disabled = false;
    } else {
      dot.disabled = true;
    }
  });
};

export {
  generateRandomNumber,
  updateScore,
  updateScoreDOM,
  toggleStartBtn,
  updateSliderHTML,
  getPosition,
  getAnimationTime,
  addPointToScore,
  resetGameBoard,
  displayElement,
  pauseDots
};
